import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LeaderboardsComponent } from './features/leaderboards/leaderboards.component';
import { LogDailyComponent } from './features/log-daily/log-daily.component';
import { ProfileComponent } from './features/profile/profile.component';

const routes: Routes = [
  { path: 'profile', component: ProfileComponent },
  { path: 'log-daily', component: LogDailyComponent },
  { path: 'leaderboards', component: LeaderboardsComponent },
  { path: '**', component: ProfileComponent },  // Wildcard route for a 404 page
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
